*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    font-family:cursive;

}
.navbar{
    background-color:#fff !important;
}
.navbar-brand{
    font-size:1.9rem !important;
    color: rgb(47, 8, 119) !important;
}
.navbar a{
    font-size:1.3rem;
    text-transform:capitalize;
    
}
.menu_active{
        font-weight:bold;
        border-bottom:1px solid rgb(75, 4, 75);
}
.navbar a:hover{
    color:orange !important;
}

@media (max-width:991px){
    .navbar-brand{
        font-size:1.5rem !important
    }
    .menu_active{
        font-weight:bold;
        border-bottom:none;
    }
}
#header{
    widows: 100%;height:80vh;
    margin-top:70px;
}
#header h1{
    margin:0 0 10px 0;
    font-size:48px;
    font-weight:700;
    color:lightsalmon;
}
#header .brand-name{
    color:#3489db;
}
#header h2{
    color:crimson;
    font-size:24px;
}
#header .btn-get-started{
    font-weight:500;
    font-size:16px;
    letter-spacing:1px;
    display:inline-block;
    padding:10px 30px;
    border-radius:50px;
    transition:0.5s;
    color:#3489db;
    border:2px solid #3489db;
    text-decoration: none;

}
#header .btn-get-started:hover{
    background:#3489db;
    color:#fff
}
.header-img{
    text-align:right;
    height:200px;
}
#header .animated{
    animation:up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0%{
        transform:translateY(10px);
    }
    100%{
        transform:translateY(-10px);
    }
}
@keyframes up-down{
    0%{
        transform:translateY(10px);
    }
    100%{
        transform:translateY(-10px);
    }
}
